.home{
    display: flex;  
    .homeContainer{       
        flex:6 ;
        .widgets{
            display: flex;
            padding: 20px;
            gap:30px;
        }
        .commit-viewer-containers{
            display: flex;
            gap: 30px;
            padding: 20px;
            
            // .commitContainer{
            //     display: flex;
            //     // flex:2;
            //     padding: 5px ,20px;
            // }
            .viewerContainer{
                display: flex;
                flex:4;
                padding:5px, 10px;
                height: 500px;
                width: 100%;
            }
            
        }        
        .listContainer {
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            border-radius: 10px;
            padding: 20px;
            margin: 20px;
        }
        .listTitle {
            font-weight: 500;
            color: gray;
            margin-bottom: 15px;
          }
}}